.bomb{
  position: absolute;
  top: 70%;
  left: 50%;

  display: block;
  width: 12.8vh;
  height: 12.8vh;
  border-radius: 100%;
  z-index: 40;
  -webkit-transform: translate(-50%,-50%) rotate(5deg);
      -ms-transform: translate(-50%,-50%) rotate(5deg);
          transform: translate(-50%,-50%) rotate(5deg);
}

.bomb:before{
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;

  display: block;
  width: 12.8vh;
  height: 12.8vh;
  border-radius: 100%;

  background-color: currentColor;
  -webkit-box-shadow: 0 0 0.2vh 0.2vh black inset;
          box-shadow: 0 0 0.2vh 0.2vh black inset;

  -webkit-transform: translate(-50%,-50%);

      -ms-transform: translate(-50%,-50%);

          transform: translate(-50%,-50%);

  z-index: 1;
}

.bomb:after{
  content: "";
  position: absolute;
  top: 15%;
  left: 8%;

  width: 50%;
  height: 50%;
  border-radius: 45%;
  border: 1vh solid transparent;
  border-left: 1vh dotted white;

  -webkit-transform: rotate(30deg);

      -ms-transform: rotate(30deg);

          transform: rotate(30deg);
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  z-index: 2;
}

/* 出っ張り */
.bomb .ledge{
  position: absolute;
  left: 50%;

  display: inline-block;
  width: 30%;
  height: 20%;
  border-radius: 15% 15% 100% 100%;
  background-color: currentColor;

  -webkit-transform: translate(-50%,-25%);

      -ms-transform: translate(-50%,-25%);

          transform: translate(-50%,-25%);

  z-index: 2;
}

/* 導火線 */
.bomb .fuse{
  position: absolute;
  top: 50%;
  left: 50%;

  display: block;
  width: 99%;
  height: 99%;
  border: 0.6vh solid transparent;
  border-top-color: #D0BAA5;
  border-right-color: #D0BAA5;
  border-radius: 100%;

  -webkit-box-sizing: border-box;

          box-sizing: border-box;
  -webkit-transform: translate(-15%,-75%) rotate(-20deg);
      -ms-transform: translate(-15%,-75%) rotate(-20deg);
          transform: translate(-15%,-75%) rotate(-20deg);

  z-index: -1;
}

.bomb .fuse:before,
.bomb .fuse:after{
  content: "＊";
  position: absolute;
  right: -.55em;
  bottom: 0;

  display: inline-block;
  font-size: 1.5em;
}

.bomb .fuse:before{
  color: red;
  -webkit-transform: translate(-50%,0) rotate(0);
      -ms-transform: translate(-50%,0) rotate(0);
          transform: translate(-50%,0) rotate(0);

}

.bomb .fuse:after{
  color: orange;
  -webkit-transform: translate(-50%,0) rotate(30deg);
      -ms-transform: translate(-50%,0) rotate(30deg);
          transform: translate(-50%,0) rotate(30deg);
}

.bomb .dust{
  position: absolute;
  top: 50%;
  left: 50%;

  width: 100%;
  height: 100%;
  -webkit-transform: translate(-50%,-50%);
      -ms-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
}



.black{
  color: black;
}



.bomb{-webkit-animation:bomb_jump 0.6s ease-in-out 6.55s forwards;animation:bomb_jump 0.6s ease-in-out 6.55s forwards;}

.bomb .fuse{ -webkit-animation:
    fuse_anim 3s 7.75s linear,
    after_exp 1s 10.75s steps(1,start) forwards; animation:
    fuse_anim 3s 7.75s linear,
    after_exp 1s 10.75s steps(1,start) forwards;
}

.bomb .fuse:before{ -webkit-animation: burning 3s linear; animation: burning 3s linear; }
.bomb .fuse:after { -webkit-animation: burning 3s linear reverse; animation: burning 3s linear reverse;}


@-webkit-keyframes bomb_jump {
    50%{
        left: 15%;
        top:25%;
    }

    100%{
        left: 50%;
        top:15%;
    }
}


@keyframes bomb_jump {
    50%{
        left: 15%;
        top:25%;
    }

    100%{
        left: 50%;
        top:15%;
    }
}

@-webkit-keyframes fuse_anim{
  49%{
    border-top-color: #D0BAA5;
  }

  50%{
    border-top-color: transparent;
  }

  100%{
    border-top-color: transparent;
    -webkit-transform: translate(-15%,-75%) rotate(-200deg);
            transform: translate(-15%,-75%) rotate(-200deg);
  }
}

@keyframes fuse_anim{
  49%{
    border-top-color: #D0BAA5;
  }

  50%{
    border-top-color: transparent;
  }

  100%{
    border-top-color: transparent;
    -webkit-transform: translate(-15%,-75%) rotate(-200deg);
            transform: translate(-15%,-75%) rotate(-200deg);
  }
}

@-webkit-keyframes burning{
  to{
    -webkit-transform: translate(-50%,0) rotate(360deg);
            transform: translate(-50%,0) rotate(360deg);
  }
}

@keyframes burning{
  to{
    -webkit-transform: translate(-50%,0) rotate(360deg);
            transform: translate(-50%,0) rotate(360deg);
  }
}


/* 爆発後 */
@-webkit-keyframes after_exp{
  to{
    opacity: 0;
  }
}
@keyframes after_exp{
  to{
    opacity: 0;
  }
}

@-webkit-keyframes message{
  from{
    opacity: 1;
  }

  to{
    font-size: 4.5em;
  }
}

@keyframes message{
  from{
    opacity: 1;
  }

  to{
    font-size: 4.5em;
  }
}



