.rhombus-field{
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    right: 0;
    top:0;
    bottom: 0;
    opacity: 0;
    margin: auto;
    z-index: 90;
    -webkit-animation: feed-in 0.001s linear 10.899s forwards;
            animation: feed-in 0.001s linear 10.899s forwards;
}

.rhombus {
    --diagonal-x: 30vw;
    --diagonal-y: 60vh;
    width: 0;
    height: 0;
    left: 0;
    right: 0;
    top:-30%;

    border-bottom: calc(var(--diagonal-y) / 2) solid var(--color);
    border-left: calc(var(--diagonal-x) / 2) solid transparent;
    border-right: calc(var(--diagonal-x) / 2) solid transparent;
    position: absolute;
    margin: auto;

    }

.rhombus::after {
    content: "";
    width: 0;
    height: 0;
    border-top: calc(var(--diagonal-y) / 2) solid var(--color);
    border-left: calc(var(--diagonal-x) / 2) solid transparent;
    border-right: calc(var(--diagonal-x) / 2) solid transparent;
    position: absolute;
    top: calc(var(--diagonal-y) / 2);
    left: calc(var(--diagonal-x) / -2);
    }

.rhombus:nth-child(1)   {-webkit-animation: pulsate 0.8s ease-out 10.8s forwards;animation: pulsate 0.8s ease-out 10.8s forwards;--color: #c93a40;}
.rhombus:nth-child(2)   {-webkit-animation: pulsate 0.8s ease-out 10.88s forwards;animation: pulsate 0.8s ease-out 10.88s forwards;--color: #d16b16;}
