#stage {
  position:absolute;
  width: 100%; height: 100%;
  overflow: hidden;
  -webkit-perspective: 20px;
          perspective: 20px;
  background-color: #FFF5EE;
}

#stage #screen {
  position: absolute;
  left: 0px;
  right: 0px;
  top: 0px;
  bottom: 0px;
  margin: auto;
  width: 100vw; height: 100vw;
}

.reverse_stage {
  position: absolute;
  left: 0px;
  right: 0px;
  top: 0px;
  bottom: 0px;
  margin: auto;
  width: calc(100% - 40px); height: calc(100% - 40px);
  overflow: hidden;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  opacity: 0;
}

/*** frame ***/
.paint__reverse {
  position: absolute;
  left: 0px;
  right: 0px;
  top: 0px;
  bottom: 0px;
  margin: auto;
  width: calc(100% - 40px); height: calc(100% - 40px);
  -webkit-transform: translateZ(50px) scale(0.01);
          transform: translateZ(50px) scale(0.01);/*scaleである地点に到達した時に本来の大きさに*/
  border: solid 1px #444;
  border-radius: 50%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-box-shadow: 0 0 0 20px #00cc22 inset;
          box-shadow: 0 0 0 20px #00cc22 inset;/*影をつけることで二重丸にできる*/
}

#screen .paint{
  position: absolute;
  left: 0px;
  right: 0px;
  top: 0px;
  bottom: 0px;
  margin: auto;
  width: calc(100% - 40px); height: calc(100% - 40px);
  border-radius: 50%;/*border-radius:50%で丸*/
  background-color: #00cc22;
  opacity: 0;
}

.paint:nth-of-type(1)  { -webkit-animation: fadeout_paint 0.7s linear 0s backwards; animation: fadeout_paint 0.7s linear 0s backwards; }
.paint:nth-of-type(2)  { -webkit-animation: fadeout_paint 0.7s linear 1.4s backwards; animation: fadeout_paint 0.7s linear 1.4s backwards; }
.paint:nth-of-type(3)  { -webkit-animation: fadeout_paint 0.7s linear 2.8s backwards; animation: fadeout_paint 0.7s linear 2.8s backwards; ;}

.reverse_stage:nth-of-type(4){ -webkit-animation:camera 0.7s linear 0.7s backwards; animation:camera 0.7s linear 0.7s backwards; }
.reverse_stage:nth-of-type(5){ -webkit-animation:camera 0.7s linear 2.1s backwards; animation:camera 0.7s linear 2.1s backwards; }
.reverse_stage:nth-of-type(6){ -webkit-animation:camera 0.7s linear 3.5s backwards; animation:camera 0.7s linear 3.5s backwards; }

@-webkit-keyframes fadeout_paint {
  0% { /* 初期状態 */
    opacity: 0;
    width: 100%; height: 100%;
    border-radius: 50%;
  }
  1%{
      opacity: 1;
      width: 100%; height: 100%;
      border-radius: 50%;
  }
  100% {
    opacity: 1;
    width: 0px; height: 0px;
    border-radius: 50%;
  }

}

@keyframes fadeout_paint {
  0% { /* 初期状態 */
    opacity: 0;
    width: 100%; height: 100%;
    border-radius: 50%;
  }
  1%{
      opacity: 1;
      width: 100%; height: 100%;
      border-radius: 50%;
  }
  100% {
    opacity: 1;
    width: 0px; height: 0px;
    border-radius: 50%;
  }

}


@-webkit-keyframes camera {
  0% {
      opacity: 0;
      -webkit-perspective: 60px;
              perspective: 60px;
  }
  1% {
      opacity: 1;
      -webkit-perspective: 60px;
              perspective: 60px;
  }
  100% {
      opacity: 1;
      -webkit-perspective: 40px;
              perspective: 40px;
  }
}


@keyframes camera {
  0% {
      opacity: 0;
      -webkit-perspective: 60px;
              perspective: 60px;
  }
  1% {
      opacity: 1;
      -webkit-perspective: 60px;
              perspective: 60px;
  }
  100% {
      opacity: 1;
      -webkit-perspective: 40px;
              perspective: 40px;
  }
}



